* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

footer,
header,
section {
  display: block; }

html,
body {
  height: 100%;
  scroll-behavior: smooth; }

body {
  font-family: "Roboto Condensed", "Arial", sans-serif; }

input,
button,
textarea {
  font-family: "Roboto Condensed", "Arial", sans-serif; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit; }

.container {
  box-sizing: border-box;
  min-width: 320px;
  padding-left: 25px;
  padding-right: 25px;
  margin: 0 auto; }
  @media (min-width: 374.98px) {
    .container {
      padding-left: 40px;
      padding-right: 40px;
      max-width: 100%; } }
  @media (min-width: 979.98px) {
    .container {
      padding-left: 10px;
      padding-right: 10px;
      max-width: 1300px; } }
  @media (min-width: 1300px) {
    .container {
      padding-left: 0;
      padding-right: 0; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content; }

.header {
  position: relative; }

.header__body {
  padding-top: 12px;
  padding-bottom: 9px;
  display: grid;
  align-items: center;
  justify-items: center;
  -moz-column-gap: 10px;
       column-gap: 10px; }
  @media (min-width: 767.98px) {
    .header__body {
      grid-template-columns: 1fr 3fr 1fr; } }

@media (min-width: 979.98px) {
  .header__logo {
    justify-self: start; } }

.header-menu__toggle {
  display: none; }
  @media (max-width: 767.98px) {
    .header-menu__toggle {
      display: block;
      padding: 5px;
      position: absolute;
      right: 20px;
      top: 20px; } }

.site-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center; }
  @media (max-width: 767.98px) {
    .site-list {
      position: absolute;
      top: 100%;
      right: 0;
      left: 0;
      z-index: -1;
      background-color: #d9f0f7;
      visibility: hidden;
      opacity: 0;
      transform: translateX(-100%);
      transition: transform 0.3s ease-out, opacity 0.3s ease-out; }
      .site-list--open {
        visibility: visible;
        opacity: 1;
        transform: translateX(0);
        z-index: 20; } }

.site-list__item {
  margin-right: 9px;
  display: flex; }
  .site-list__item:last-child {
    margin-right: 0; }

.site-list__link {
  padding: 5px;
  font-size: 16px;
  line-height: 19px;
  color: #4f4f4f;
  cursor: pointer; }

.header-menu__actions {
  height: 100%; }
  @media (min-width: 767.98px) {
    .header-menu__actions {
      justify-self: end; } }

.header-actions__item {
  height: 100%;
  display: flex;
  align-items: center; }

.header-actions__link {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px; }
  .header-actions__link span {
    white-space: nowrap;
    color: #3e0d41; }

.header-actions__link--tel svg {
  margin-right: 10px; }

.promo {
  min-height: 648px;
  padding-top: 110px;
  padding-bottom: 74px;
  background: gray;
  background-size: cover, 100% auto;
  background-repeat: no-repeat; }
  @media (min-width: 767.98px) {
    .promo {
      background-image: linear-gradient(rgba(16, 15, 15, 0.66), rgba(16, 15, 15, 0.66)), url("../img/car-ibg-1-tablet.jpg");
      background-position: 0 0, center top; } }
  @media (min-width: 979.98px) {
    .promo {
      background-image: linear-gradient(rgba(16, 15, 15, 0.66), rgba(16, 15, 15, 0.66)), url("../img/car-ibg-1.jpg");
      background-position: 0 0, -28px bottom;
      background-size: cover, 2004px auto; } }
  @media (min-width: 2000px) {
    .promo {
      background-position: center bottom; } }

.promo__container {
  display: grid;
  justify-content: space-between;
  grid-template-columns: 1fr;
  row-gap: 20px; }
  @media (min-width: 979.98px) {
    .promo__container {
      grid-template-columns: 498px 400px; } }

.promo__text-block {
  color: #ffffff;
  width: 100%; }

.promo__text {
  font-weight: normal;
  font-size: 18px;
  line-height: 18px;
  margin-bottom: 32px;
  text-transform: uppercase; }

.promo__title {
  font-weight: bold;
  font-size: 52px;
  line-height: 62px;
  margin-bottom: 37px;
  text-transform: uppercase; }

.promo__text-bottom {
  font-weight: normal;
  font-size: 18px;
  line-height: 24px; }

.form-body {
  color: #ffffff; }

.form {
  width: 100%;
  padding: 36px 32px;
  background: rgba(51, 51, 51, 0.6);
  border-radius: 6px;
  display: grid;
  grid-template-columns: 1fr; }

.form__title {
  font-weight: normal;
  font-size: 26px;
  line-height: 26px;
  margin-bottom: 46px; }

.form__label {
  position: relative;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 12px;
  margin-bottom: 20px; }
  .form__label:last-of-type {
    margin-bottom: 34px; }

.form__input {
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  height: 48px;
  padding: 0 12px;
  border-radius: 4px;
  border: 1px solid #fcfae1; }
  .form__input::-moz-placeholder {
    color: rgba(0, 0, 0, 0.5); }
  .form__input::placeholder {
    color: rgba(0, 0, 0, 0.5); }

.form__input--error {
  box-shadow: inset 0 0 10px 0 red; }
  .form__input--error + .form__error-wrap {
    display: block; }

.form__error-wrap {
  display: none;
  position: absolute;
  width: 100%;
  height: auto;
  z-index: 10;
  bottom: 3px;
  left: 0;
  color: red;
  text-align: center; }

.form__btn-submit {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  height: 48px;
  margin-bottom: 30px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  background: linear-gradient(91.17deg, #eb5d56 0.01%, #390a40 100.01%);
  border-radius: 4px; }

.form__personal-data-text {
  font-weight: normal;
  font-size: 13px;
  line-height: 18px;
  letter-spacing: 0.5px; }

.form__personal-data-link {
  color: #ffffff;
  cursor: pointer; }

.footer__container {
  padding-top: 44px;
  padding-bottom: 20px;
  display: grid;
  justify-items: center; }
  @media (min-width: 767.98px) {
    .footer__container {
      justify-content: space-between;
      grid-template-columns: 204px -webkit-min-content 204px;
      grid-template-columns: 204px min-content 204px; } }

.copy-block {
  padding-top: 14px;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px; }

.copy-block__item {
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  color: #828282; }

.copy-block__link--personal-data {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-color: #828282;
          text-decoration-color: #828282;
  cursor: pointer; }

.footer-logo {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-self: start; }

.footer-logo__icon {
  margin-bottom: 6px; }

.footer-logo__text {
  font-weight: normal;
  font-size: 18px;
  line-height: 18px;
  text-transform: uppercase;
  background: linear-gradient(91.17deg, #eb5d56 0.01%, #390a40 100.01%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.footer-contacts {
  padding-top: 14px;
  display: flex;
  flex-direction: column; }

.footer-contacts__item {
  margin-bottom: 16px; }
  .footer-contacts__item:last-child {
    margin-bottom: 0; }

.footer-contacts__link {
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: #4f4f4f; }
